import React, { Component } from 'react';
import WardcardNote from './Wardnote'
import Wardbedinformation from './WardBedinformation'
import Wardschedule from './Wardschedule'
import './Wardinfoindex.scss'
import * as ApiProvider from '../API/ApiProvider';

const SCHEDULE_TYPE_EVENT = "äºä»¶";

class Wardinfoindex extends Component {
    state = {
        newannocestate: true,
        scheduleRawData: [],
        scheduleData: []
    };

    componentDidMount() {
        ApiProvider.getAllSchedule()
            .then(data => {
                console.log("getAllSchedule OK " + JSON.stringify(data.data));

                const itemList = data.data;
                let refactoredList = [];
                itemList.forEach(item => {
                    const itemTimeStr = item.time.split('T')[0];
                    let foundItemIndex = refactoredList.findIndex(element => element.time === itemTimeStr);
                    if (foundItemIndex === -1) {
                        //not found, create one
                        let newItem = {
                            time: itemTimeStr,
                            timestamp: item.time,
                            scheduleList: []
                        }
                        let newEvent = {
                            type: item.type,
                            timestamp: new Date(item.time).getTime(),
                            writer: (item.type === SCHEDULE_TYPE_EVENT) ? "æé«å¸«" : "å¼µé«å¸«",
                            name: item.name,//(item.type === SCHEDULE_TYPE_EVENT) ? "æï¼¯ï¼¯" : "é»ï¼¯ï¼¯",
                            content: item.context,
                            bednumber: item.bednumber
                        }
                        newItem.scheduleList.push(newEvent);
                        refactoredList.push(newItem);
                    } else {
                        //found, update item
                        let newEvent = {
                            type: item.type,
                            timestamp: new Date(item.time).getTime(),
                            writer: (item.type === SCHEDULE_TYPE_EVENT) ? "æé«å¸«" : "å¼µé«å¸«",
                            name: item.name,//(item.type === SCHEDULE_TYPE_EVENT) ? "æï¼¯ï¼¯" : "é»ï¼¯ï¼¯",
                            content: item.context,
                            bednumber: item.bednumber
                        }
                        refactoredList[foundItemIndex].scheduleList.push(newEvent);
                    }
                });
                this.setState({
                    scheduleRawData: data.data,
                    scheduleData: refactoredList
                });
            })
            .catch(err => {
                console.log("getAllSchedule NG " + JSON.stringify(err));
            });
    }

    getDataFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const dataOfDayIndex = this.state.scheduleData.findIndex(element => element.time === dateStr);
        const dataOfDay = (dataOfDayIndex === -1) ? { scheduleList: [] } : this.state.scheduleData[dataOfDayIndex];
        return dataOfDay;
    }

    sendData = () => {
        this.props.parentCallback(null);
    }

    render() {
        const dataOfDay = this.getDataFromTimestamp(new Date().getTime());
        return (
            <div className='Wardinfoindexcontent'>
                <centerstyle>
                    <t1 style={{ fontSize: "18px", color: "rgba(0, 0, 0, .7)", fontWeight: "600px" }}>NICUç¸½è¦½</t1>
                </centerstyle>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "5px" }}>
                    <WardcardNote></WardcardNote>
                    <Wardschedule source={0} editstatus={false} scheduleData={dataOfDay}></Wardschedule>
                </div>
                <Wardbedinformation></Wardbedinformation>
            </div>
        );
    }
}

export default Wardinfoindex;
