import React, { Component } from 'react';
import RTbreathetable from './RTbreathetable'
import { DatePicker } from 'antd';
import * as FilterReg from '../../Function/FiliterReg'
import X2JS from 'x2js';
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as QSPEList from '../../Resource/QSPE'

class RTbreathe extends Component {
    state = {
        PickerDateRange: [moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD'), moment()],
        preCID: null,
        QSPE: []
    }
    componentDidMount() {
        const { caseinfo, source, PickerDate } = this.props
        const Timer = this.formatCrossectionTimer()
        this.setState({ preCID: caseinfo.CASEID })
        this.getQSPEApi(caseinfo.CASEID, Timer[0], Timer[1])

    }
    componentDidUpdate() {
        const { caseinfo } = this.props
        const CID = caseinfo.CASEID
        const preCID = this.state.preCID
        if (CID !== preCID) {
            const Timer = this.formatCrossectionTimer()
            this.setState({ preCID: caseinfo.CASEID })
            this.getQSPEApi(caseinfo.CASEID, Timer[0], Timer[1])
        }
    }
    getQSPEApi(CID, st, et) {
        const xm2 = new X2JS()
        console.log(CID, st, et)
        const start = moment(st).format("YYYYMMDDHHmm")
        const end = moment(et).format("YYYYMMDDHHmm")
        ApiProvider.getQSPE(CID, start, end)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const result = xm2.xml2js(data.data).outmsg
                if (Number(result.ret) === 0) {
                    const resultData = result.resconts.rescont
                    if (Array.isArray(resultData)) {
                        this.setState({ QSPE: resultData })
                    }
                    else {
                        this.setState({ QSPE: [resultData] })
                    }
                }
                return []
            })
            .catch(err => {
                console.log("getQSPE NG " + JSON.stringify(err))
            });
        // this.setState({ QSPE: QSPEList.data })
    }

    formatCrossectionTimer = () => {
        const { searchbar, PickerDate } = this.props
        const dateRange = this.state.PickerDateRange

        const temp_st = dateRange[0].valueOf()
        const temp_et = dateRange[1].valueOf()

        const et = searchbar ? temp_et : moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000)
        const st = searchbar ? temp_st : et - (3 * 24 * 60 * 60 * 1000)
        return [st, et]
    }

    createRTbreathetable = () => {
        const { caseinfo } = this.props
        const QSPE = this.state.QSPE
        const Timer = this.formatCrossectionTimer()
        let DataList = FilterReg.QSPERegList(QSPE)
        DataList = DataList.filter(data => {
            let time = moment(data.Date + data.Time, "YYYYMMDDHHmm").valueOf()
            return Timer[0] <= time && time <= Timer[1]
        })
        console.log(DataList)
        return <RTbreathetable DataList={DataList} caseinfo={caseinfo}></RTbreathetable>
    }

    render() {
        const { searchbar } = this.props
        const { RangePicker } = DatePicker;

        return (
            <div style={{ marginBottom: "20px" }}>
                <div style={{ marginBottom: "5px",display: "flex", alignItems: 'center' }}>
                    <t1>å¼å¸å¨è¨­å®ç´é</t1>
                    <div style={{ display: "grid", width: "250px", gridColumnGap: "10px" }}>
                        {searchbar ? <RangePicker defaultValue={this.state.PickerDateRange} onChange={(date, datestring) => { this.setState({ PickerDateRange: date }) }} allowClear={false} /> : null}
                    </div>
                </div>
                {this.createRTbreathetable()}
            </div>
        );
    }
}

export default RTbreathe
