import React, { Component } from 'react';
import RTtable from './RTtable'
import { DatePicker } from 'antd';
import moment from 'moment';

class GAS extends Component {
    state = {
        PickerDateRange: [moment(this.props.caseinfo.TRANSINDT, 'YYYYMMDD'), moment()]
    }

    formatRangeTimer = () => {
        const { searchbar, PickerDate } = this.props
        const dateRange = this.state.PickerDateRange

        const temp_st = dateRange[0].valueOf()
        const temp_et = dateRange[1].valueOf()

        const et = searchbar ? temp_et : moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000)
        const st = searchbar ? temp_st : et - (3 * 24 * 60 * 60 * 1000)
        // if (!source) {
        //     const et = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000)
        //     const st = et - (3 * 24 * 60 * 60 * 1000)
        //     return [st, et]
        // }

        // if (st === et) {
        //     return [st, st + (24 * 60 * 60 * 1000)]
        // }
        return [st, et]

    }

    render() {
        const { searchbar, caseinfo } = this.props
        const { RangePicker } = DatePicker;
        const fotmattimer = this.formatRangeTimer()

        return (
            <div style={{ marginBottom: "20px" }}>
                <t1>GASç´¯ç©å ±å</t1>
                <div style={{ marginBottom: "5px", display: "grid", width: "250px", gridColumnGap: "10px" }}>
                    {searchbar ? <RangePicker defaultValue={this.state.PickerDateRange} onChange={(date, dateString) => { this.setState({ PickerDateRange: date }) }} allowClear={false} /> : null}
                </div>
                <RTtable Timer={fotmattimer} caseinfo={caseinfo}></RTtable>
            </div>
        );
    }
}

export default GAS
