import React, { Component } from 'react';
import GAS from '../CrossSectionView/GAS'
import RTbreathe from '../CrossSectionView/RTbreathe'
import './RTview.scss'
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

class RTview extends Component {
    state = {
        QSPE: [],
        PickerDate: moment(moment().format('YYYYMMDD')).valueOf()
    }
    componentDidMount() {
        this.API_DATA(this.state.PickerDate)
    }
    API_DATA(Datetime) {
        const xm2 = new X2JS()
        const { caseinfo } = this.props
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                console.log(parsedata)
                if (Array.isArray(parsedata)) {
                    this.setState({
                        QSPE: parsedata
                    })
                }
                else {
                    const arr = []
                    arr.push(parsedata)
                    this.setState({
                        QSPE: arr
                    })
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    render() {
        const { caseinfo } = this.props

        return (
            <div className='rtviewlayout'>
                <div style={{ display: "grid", gridTemplateColumns: '1fr 1fr', gridColumnGap: "5px" }}>
                    <GAS caseinfo={caseinfo} searchbar={true} ></GAS>
                    <RTbreathe caseinfo={caseinfo} searchbar={true} QSPE={this.state.QSPE}></RTbreathe>
                </div>
            </div>
        );
    }
}

export default RTview
